import React from 'react'
import { imgGanso } from '../../../../helpers/helper_imagen_ganso'
import { Actividad } from '../../Actividad'
import imgCuadroEjemplo from "../assets/img/EmocionalActividadEjemplo.png"
import { DescargablesActFomento } from './DescargablesActFomento'

export const ActividadDeFomentoEmocional = () => {
    return (
        <>
            <h2 className='text-center my-4'>Actividad de fomento - Emocional</h2>

            <Actividad src={imgGanso.lupa_celular} title="Actividad de fomento!"
                text={`<br><p class="text-center">Esta es una actividad de fomento. Eso significa que es un ejercicio para que practiques la habilidad orientada en el mdulo a tu ritmo y cuando tu desees. Completamente voluntario. Los cuatro botones abajo presentados te permitirn: a) descargar una versin en pdf que puedes imprimir si quieres, b) una versin en Word editable para que llenes en tu celular, c) una versin en Excel editable para que llenes en tu celular y d) una versin en Excel que se unir a tu nube en Drive para que llenes tambin cuando quieras. Lo mejor de estas opciones? Solo t tienes acceso a estas y solo t puedes ver lo que escribes all. Nadie ms. Esperamos que sea una herramienta que te ayude a fortalecer tu salud mental.
                        <br>
                        `
                }
                showIcon={true} />

            <Actividad
                siImange={true}
                src={imgGanso.explicando}
                style={{ width: "110px", height: "110px" }}
                title={"Esta plantilla te permitir mantener un registro de la gestin de tus emociones. Recuerda que puedes escribir cuanto quieras. El limite lo pones t. Lo importante es que intentes practicar a diario, escribir al menos cada da. Ten en cuenta cada columna:"}
                text={`<ol class="ms-4">
                                <li type="disc"><b>Situacin: </b>Indica la situacin que te haya producido la emocin. No tiene que ser algo grande. Las situaciones del da a da nos provocan diversas emociones. Es bueno que practiques con situaciones cotidianas para identificar lo que sucede a travs de la semana. </li>
                                <li type="disc"><b>Sensaciones: </b>Qu experimentaste ante esa situacin? Cmo sentiste tu cuerpo en general? Internamente hubo algn cambio? Descrbelo con tus palabras sin necesidad de ser tcnico. Es un diario de ti para ti. </li>
                                <li type="disc"><b>Emociones: </b>Describe con tus palabras que emociones principalmente te llenaron. Puedes colocar las que has nombrado durante el modulo. Una situacin puede ocasionarte varias emociones. Identificarlas y darles un nombre te ayuda a hacerlo consciente. </li>
                                <li type="disc"><b>Pensamientos: </b>Qu pensamientos te atravesaron la mente en ese momento? Qu considerabas? Hacia dnde te inclinabas a pensar? </li type="disc">
                                <li type="disc"><b>Acciones: </b>Qu te provoco hacer e hiciste finalmente? Cmo afrontaste la situacin o la emocin que la produjo? </li>
                                <li type="disc"><b>Consecuencias: </b>Qu paso despus de hacer eso? Qu impacto tuvo en ti o en los dems?  </li>
                            </ol>`}
                showIcon={false} />

            <div className='d-flex flex-column justify-content-center align-items-center'>
                <h4 className='my-4'>Ejemplo</h4>
                <img src={imgCuadroEjemplo} alt="" style={{ width: "800px" }} />
                <p className='mt-2 mb-4 text-center'><small><b>Fuente:</b> Elaboracin propia inspirado en Hervas y Moral (2017)</small></p>
            </div>

            <DescargablesActFomento />
        </>

    )
}
