import React from 'react'
import { Actividad } from "../Actividad"
import { Descripcion } from '../Descripcion'
import { imgGanso } from '../../../helpers/helper_imagen_ganso'
import { FcSurvey } from 'react-icons/fc'
import Carrusel from '../Carrusel'
import { TecnicaEmocional } from '../../../helpers/helperTecnicaEmocional';


export const RelajacionProgresiva = () => {
    return (
        <>
            <h2 className='text-center'>Tcnica de Relajacin Progresiva de Jacobson</h2>

            <div className='row justify-content-center align-items-center'>
                <div className='col-lg-12'>
                    <Actividad src={imgGanso.meditando} title="Para qu me servir esta actividad?"
                        text={`En la imagen a continuacin se muestra la forma en la que se realizar sta actividad, puedes hacerlo si te encuentras en una situacin de estrs en este momento, si tienes tiempo puedes realizar todos los grupos musculares, sin embargo si realizas solo uno tambin tendrs una sensacin de relajacin muscular que querrs repetir. Te recomendamos estar sentado en una posicin cmoda. `} showIcon={false} />
                </div>
            </div>

            <div className='row justify-content-center align-items-center'>
                {TecnicaEmocional.map((carrusel, index_tecnica) => (
                    <div className='col-lg-6' key={index_tecnica}>
                        <Carrusel
                            carrusel={carrusel}
                            key={index_tecnica}
                        />
                    </div>
                ))}
            </div>

            <Descripcion
                title={"Para finalizar"}
                text={`Es necesario tener en cuenta que este ejercicio permite tener los msculos trabajados en estado de relajacin, de tal manera que cada vez que se sienta tensin en estas reas se podr evocar a la sensacin de relajacin del mismo y relajar automticamente el mismo. Al terminar los ejercicios permanezca unos minutos disfrutando la sensacin de relajacin centrando la atencin en las sensaciones agradables que propici el ejercicio.`}
                icon={<FcSurvey style={{ fontSize: '2rem' }} />}
                color="#03A9F4"
            />

        </>
    )
}
