import React,{useState,useContext} from 'react'
import { imgGanso } from '../../../../helpers/helper_imagen_ganso'
import { Actividad } from '../../Actividad'
import imgAutoRegistro from "../assets/img/cuadroAutorregistro.png"
import { ErrorAlert, Correct_Alert } from '../../../../helpers/helper_Swal_Alerts'
import { BotonContext } from '../../../../context/BotonContext'
import { AvanceContext } from '../../../../context/AvanceContext'
import { AuthContext } from '../../../../context/AuthContext'
import { DescargablesActFomento } from '../actividadDeFomento/DescargablesActFomento'

export const ActividadDeFomentoEmocional = () => {

    const { setBotonState } = useContext(BotonContext)
    // Datos del avance que lleva el usuario
    const { AvanceState } = useContext(AvanceContext);
    //TODO: se debe validar si esta actividad se hizo anteriormente
    //con el fin de mostar directamente las descargas de los archivos

    const { authState } = useContext(AuthContext)
    const { userInfo } = authState

    const [ActividadCompletada, setActividadCompletada] = useState(false)

    const [Datos, setDatos] = useState({
        Texto1: "",
        Texto2: "",
        Texto3: "",
        Texto4: "",
        Texto5: ""
    })

    const validarTextArea = (name) => {
        let textArea = document.getElementsByName(name)[0].value;
        return textArea.trim().length >= 1
    }

    const handleBtnEnviar = () => {
        const arrTextArea = ["Texto1", "Texto2", "Texto3", "Texto4", "Texto5"]
        for (let textArea of arrTextArea) {
            if (!validarTextArea(textArea)) {
                ErrorAlert("Ups algo ha salido mal.", "Parece que has dejado uno o varios campos en blanco")
                return false
            }
        }

        setActividadCompletada(true)
        Correct_Alert("Felicidades has realizado tu autorregistro!", `Cuack est orgulloso de t has completado tu registro! Identificar aquellos patrones en las emociones y pensamientos en torno a la procrastinacin de actividades nos permite hacer frente a los mismos y encontrar estrategias para afrontar estos de unamejormanera.
            <br/>
            `)
    }

    const handleChange = (event) => {
        setDatos({
            ...Datos,
            [event.target.name]: event.target.value
        })
    }

    return (
        <div className="mx-4">

            <h2 className='text-center'>Autorregistro</h2>


            <div className='row justify-content-center align-items-center'>
                <div className='col-lg-10'>
                <Actividad src={imgGanso.lupa_celular} title="Actividad de fomento!"
                text={`<br><p class="text-center">Esta es una actividad de fomento. Eso significa que es un ejercicio para que practiques la habilidad orientada en el mdulo a tu ritmo y cuando tu desees. Completamente voluntario. Los cuatro botones abajo presentados te permitirn: a) descargar una versin en pdf que puedes imprimir si quieres, b) una versin en Word editable para que llenes en tu celular, c) una versin en Excel editable para que llenes en tu celular y d) una versin en Excel que se unir a tu nube en Drive para que llenes tambin cuando quieras. Lo mejor de estas opciones? Solo t tienes acceso a estas y solo t puedes ver lo que escribes all. Nadie ms. Esperamos que sea una herramienta que te ayude a fortalecer tu salud mental.
                        <br>
                        `
                }
                showIcon={true} />
                </div>
                <div className='col-lg-10'>
                    <Actividad
                        siImange={true}
                        src={imgGanso.explicando}
                        style={{ width: "110px", height: "110px" }}
                        title="Descripcion de la actividad."
                        text={`La procrastinacin sucede cuando el individuo se enfrenta a tareas o situaciones que son interpretadas por este como aversivas (difciles, aburridas, etc.) es por eso que esta conducta se relaciona con la reparacin de dao en el estado de nimo a corto plazo y la regulacin de las emociones, en otras palabras, la persona que procrastina una actividad se centra en cambiar el estado de nimo negativo y no en el desarrollo de la tarea, por lo tanto, se suele desplazar la tarea inicial por otras que generen una satisfaccin a corto plazo y restablezcan el estado de nimo positivo, a pesar de esto no se puede dejar de lado el malestar que genera dicho aplazamiento a futuro, convirtindose en un ciclo: aplazas para no sentirte mal y luego te sientes mal por aplazar <i>(Sirois y Pychyl, 2013)</i>
                <br>
                Teniendo en cuenta lo anterior es importante que identifiquemos cuales son las actividades que ms tendemos a aplazar y los sentimientos asociados a las mismas para as lograr afrontarlas de una mejor manera. Para esto te ayudars con el siguiente cuadro de autorregistro, el cual tambin te orientar con un ejemplo (basado en hechos reales)
                `}
                        showIcon={false} />
                </div>

                <div className='d-flex flex-column justify-content-center align-items-center'>
                    <h4 className='my-4'>Ejemplo</h4>
                    <img src={imgAutoRegistro} alt="" />
                    <p className='mt-2 mb-4 text-center'><small><b>Fuente.</b> Elaboracin propia basada en Beck (1995, 2000) </small></p>
                </div>
            </div>

            <DescargablesActFomento /> 
            
        </div>
    )
}
