import React from 'react'
import { imgGanso } from '../../../../helpers/helper_imagen_ganso'
import { Actividad } from '../../Actividad'

import imgCuadroEjemplo from "../assets/img/PiensaloActividadEjemplo.png"
import { DescargablesActFomento } from './DescargablesActFomento'

export const ActividadDeFomentoPiensalo = () => {
    return (
        <>
            <h2 className='text-center my-4'>Actividad de fomento - Piensalo</h2>

            <Actividad src={imgGanso.lupa_celular} title="Actividad de fomento!"
                text={`<br><p class="text-center">Esta es una actividad de fomento. Eso significa que es un ejercicio para que practiques la habilidad orientada en el mdulo a tu ritmo y cuando tu desees. Completamente voluntario. Los cuatro botones abajo presentados te permitirn: a) descargar una versin en pdf que puedes imprimir si quieres, b) una versin en Word editable para que llenes en tu celular, c) una versin en Excel editable para que llenes en tu celular y d) una versin en Excel que se unir a tu nube en Drive para que llenes tambin cuando quieras. Lo mejor de estas opciones? Solo t tienes acceso a estas y solo t puedes ver lo que escribes all. Nadie ms. Esperamos que sea una herramienta que te ayude a fortalecer tu salud mental.
                        <br>
                        `
                }
                showIcon={true} />

            <Actividad
                siImange={true}
                src={imgGanso.explicando}
                style={{ width: "110px", height: "110px" }}
                title={"Esta plantilla te permitir mantener un registro diario de pensamientos distorsionados. Recuerda que puedes escribir cuanto quieras. El limite lo pones t. Lo importante es que intentes practicar a diario, escribir al menos cada da."}
                text={`
                <p>
                Hemos adquirido herramientas que nos permiten identificar distorsiones cognitivas y formas de poder cuestionarlas, no obstante, el proceso para lograr modificarlas no se logra en un da, por eso es necesario que continuamente puedas ejercitar el proceso de identificacin para que sea mucho ms fcil despus encontrar una respuesta racional. A continuacin, encontraras un diario para el registro de pensamientos distorsionados, es importante que lo diligencies todos los das.
                </p>
                <p>
                <b>Instrucciones:</b> Cuando experimente una emocin desagradable, describa la situacin que pudo motivar la aparicin de la emocin (Si la emocin tuvo lugar mientras usted pensaba algo, antelo). A continuacin, anote el pensamiento automtico asociado con la emocin. Anote el grado de creencia en este pensamiento: 0 = nada en absoluto; 100= totalmente. En la evaluacin de la intensidad de la emocin, 1 = un leve indicio; 100 = la mxima posible, evalu del 1 al 100 el nivel de respuesta racional y el resultado. A continuacin, se muestra un ejemplo. 
                </p>
                `}
                showIcon={false} />

            <div className='d-flex flex-column justify-content-center align-items-center'>
                <h4 className='my-4'>Ejemplo</h4>
                <img src={imgCuadroEjemplo} alt="" />
                <p className='mt-2 mb-4 text-center'><small><b>Fuente:</b> Tomado de Beck et al (1983)</small></p>
            </div>

            <DescargablesActFomento />
        </>

    )
}
